li.experience-item{
    list-style: none;
    padding-bottom: 5px;
}
ul.experience-items{
    margin-block-start: 0em;
    margin-block-end: 0em;
    padding-inline-start:3em;
}
li.experience-item::before{
    content: "\2b21";
    color: var(--secondary);
    display: inline-block;
    width: 1.5em;
    margin-left: -1.5em;
}
.experience-period.row{
    padding: 20px 10px;
    border-top: 1px solid var(--light);
    display:grid;
    grid-template-columns: 155px auto;
    grid-template-rows:auto;
    grid-gap: 10px;
    grid-auto-flow: row;
}
.experience-placement.row:last-child{
    border-bottom:none;
}
.experience-period .location{
    grid-column:1 / span 2;
    grid-row-end: span 1;
    font-size:20px;
    margin-block-start: 0em;
    margin-block-end: 0em;
    color:var(--primary);
}
.experience-location{
    grid-column:1 / span 1;
}
.experience-title{
    color:var(--secondary);
    grid-column: 2 / span 1;
}
.experience-date{
    grid-column:1;
    grid-row-end:span 1;
    justify-self:center;
}
.experience-items{
    grid-column: 1/span 2;
}
