:root{
    --primary:#4eb310;
    --secondary:#083805;
    --accent:#ff4a4a;
    --dark:#0d170e;
    --shadow:#0d1c27;
    --light:#aab8c0;
    --background:#ffffff;
    --dark-screen:#000000e6;
    --light-screen:#fffffa36;
    --title-img:url(/test/static/media/TileableWatermelon2.f70de74b.png);
    --body-font:'Montserrat', 'Helvetica', sans-serif;
}
