body{
    color:#0d170e;
    color:var(--dark);
    background:#ffffff;
    background:var(--background);
}
#title{
    width:100%;
}

.App .content{
    max-width:1024px;
    margin: 0px 20px 0px 20px;
    padding: 20px 5px;
}
.title-image{
    background:url(/test/static/media/TileableWatermelon2.f70de74b.png);
    background:var(--title-img);
    background-position:center;
    background-size:auto 200px;
    height:200px;
    width:100%;
    display:block;
}
.page-title{
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;


}
.page-title  > * {
    color:#0d170e;
    color:var(--dark);
    text-align: center;
    font-size:100px;
    font-family: 'Red Hat Text', sans-serif;
    font-weight: 400;
    text-shadow: 2px 3px 4px #0d1c27;
    text-shadow: 2px 3px 4px var(--shadow)
}
.page-title .first-name{
    font-size: 62px;
    display: block;
    margin-bottom: -22px;
    line-height: 47px;
    position: relative;
    z-index: 1;
}
.page-title .preferred-name{
    font-family: 'Princess Sofia', cursive;
    font-size: 112px;
    margin-right: -15px;
    line-height: 100px;
    margin-top: -15px;
    text-shadow:none;
}
.section-title{
    color:var(--pop);
    display: block;
    font-size: 2em;
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
    -webkit-margin-after: 0px;
            margin-block-end: 0px;
    padding-bottom: 10px;
    font-weight: bold;
}
.content-section{
    padding:10px 10px;
    border-bottom: 1px solid #ff4a4a;
    border-bottom: 1px solid var(--accent,var(--primary));
}

@media only print{
    .App .content{
        padding: 0px;
    }
    .page-title{
        background-position:center middle;
    }
}

var(--shadow)
.hex-pie{
  margin:5px;
}
.hex-name-plate{
  font-family:sans-serif;
  height:80px;
  width:80px;
  background:white;
  position:absolute;
  left: 17px;
  top: 29px;
  display:flex;
  flex-direction: column;
  text-align:center;
  justify-content: center;
  border-radius:50%;
  box-shadow:1px 2px 7px var(--shadow);
}
.hex-title{
    text-transform: capitalize;
    text-align: center;
}
.hex-info{
    margin-top:-5px;
    color: white;
    color: var(--light,white);
    font-size:80%;
    position: absolute;
    bottom: 10px;
    align-self: center;
}
.hex-hover-box{
    position: absolute;
    height: 200px;
    width: 172px;
    background: var(--dark-screen);
    color: white;
    display:flex;
    align-items:center;
    top: -200px;
}
.expandable.hex:hover{
    z-index: 1;
}
.expandable.hex:hover .hex-hover-box{
    top:0px;
}
.hex{
    margin: 5px;
}
.hex .hex-mask{
    left: 0px;
    top:0px;
}
.expandable.hex:hover .hex-mask{
    left:0px;
    top:0px;
    height: 200px;
    width: 172px;
}
.hex > .hex-mask{
    left:0px;
    right:0px;
}
.expandable.hex:hover > .hex-mask {
    left: -29px;
    top: -32px;
}
.expandable.hex:hover > .hex-mask > .hex-mask > .hex-mask  .hex-mask{
    height: 300px;
}
.expandable.hex:hover .hex-name-plate{
    left: 45px;
    top: 58px;
}


.hex-hover-box-text{
    font-size:14px;
    padding:10px;
    color:transparent;
    transition: color 0.1s linear 0.1s;
}
.expandable.hex:hover .hex-hover-box-text{
    color:white;
}

.hex-content{
  height:100%;
  width:100%;
  position:absolute;
  top:0px;
  left:0px;
  overflow-x:hidden;
  overflow-y:hidden;
  background:white;
}
.hex-pie .hex-left, .hex-pie .hex-right{
  position:absolute;
  width:50%;
  height:100%;
  overflow-x:hidden;
  overflow-y:hidden;
}
.hex-pie .hex-right{
  left: 50%;
}

.hex-left .hex-fill{
  left:100%;
  top:-25%;
  transform-origin: left center;

}
.hex-right .hex-fill{
  right:150%;
  top:-25%;
  transform-origin: right center;
}
.hex-fill{
  position:relative;
  -webkit-print-color-adjust: exact;
  background-color: var(--primary), black !important;
  background-color: var(--accent,var(--primary), black) !important;
  content:" ";
  width:150%;
  height:150%;

}
.hex-danger > .hex-fill{
    background-color: black;
    background-color: var(--danger,black);
}

.hex-mask{
  transform: rotate(120deg);
  width:115px;
  height:135px;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  overflow-x:hidden;
  overflow-y:hidden;
  position:relative;
}
.hex {
    position:relative;
    height:135px;
    width:115px;
}
.hex .hex-mask ,.hex-name-plate{
    /* -webkit-transition: height 0.1s linear, width 0.1s linear,top 0.1s linear, left 0.1s linear;
    -moz-transition: height 0.1s linear, width 0.1s linear,top 0.1s linear, left 0.1s linear;
    -o-transition: height 0.1s linear, width 0.1s linear,top 0.1s linear, left 0.1s linear;
    transition: height 0.1s linear, width 0.1s linear,top 0.1s linear, left 0.1s linear; */
    transition: all 0.1s ease;
}

.hex > .hex-mask > .hex-mask > .hex-mask .hex-mask {
    height:170px;
}
.hex > .hex-mask > .hex-mask > .hex-mask > .hex-mask{
    box-shadow:inset -1px -7px 10px 0px black;
    box-shadow:inset -1px -7px 10px 0px var(--shadow,black);
}
.hex > .hex-mask > .hex-mask > .hex-mask > .hex-mask > .hex-mask{
    box-shadow: inset -6px 7px 12px 0px black;
    box-shadow: inset -6px 7px 12px 0px var(--shadow,black);
}
.hex > .hex-mask > .hex-mask > .hex-mask > .hex-mask > .hex-mask > .hex-mask{
  box-shadow: inset 3px 4px 12px 0px black;
  box-shadow: inset 3px 4px 12px 0px var(--shadow,black);
}
.hex-icon-frame{
    margin:5px;
}
.hex-icon-frame .hex-icon{
    top: 30px;
    font-size: 80px;
    position: absolute;
    color: #ffffff66;
    color: var(--light-screen, #ffffff66);
}

.hex-icon-frame .hex-content{
    background-color: var(--primary),black;
    background-color: var(--accent,var(--primary),black);
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
}
.hex-icon-frame .hex-icon-title{
    text-shadow: 1px 1px 3px black;
    text-shadow: 1px 1px 3px var(--shadow,black);
    color: white;
    font-weight: 600;
    width: 100%;
    text-align: center;
    text-transform: capitalize;
    position: absolute;
    padding:4px;
}

.hex-row{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 15px;
    padding-top: 36px;
}
.hex-row > * {
    margin-top:-32px;
}
/*smallest screen (mobile sm) min width  2 then 1*/
@media only screen{
    .hex-row{
        width:240px;
    }
    .hex-row > .hex:nth-child(2n+1){
        margin-left:67px;
    }
}
/*mobile medium (min 375) 2 and then 1*/
@media  only screen and (min-width: 375px){

    .hex-row > .hex:nth-child(2n+1){
        margin-left:5px;/*cancel last break point*/
    }
    .hex-row{
        width:250px;
    }
    .hex-row > .hex:nth-child(3n){
        margin-left:67px;
    }
}
/*v large phones 3 and then 2? */
@media  only screen and (min-width: 475px){
    .hex-row > .hex:nth-child(3n){
        margin-left:5px;/*cancel prev*/
    }

    .hex-row{
        width:375px;
    }
    .hex-row > .hex:nth-child(5n-1){
        margin-left:67px;
    }
}

/*tablets 4 and then 3*/
@media  only screen and (min-width: 625px){
    .hex-row > .hex:nth-child(5n-1){
        margin-left:5px;/*cancel prev*/
    }

    .hex-row{
        width:500px;
    }
    .hex-row > .hex:nth-child(7n-2){
        margin-left:67px;
    }
}

/* 5 and then 4*/
@media  only screen and (min-width: 775px){
    .hex-row > .hex:nth-child(7n-2){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 630px;
    }
    .hex-row > .hex:nth-child(9n-3){
        margin-left:67px;
    }
}

/* 6 and then 5*/
@media  only screen and  (min-width: 930px){
    .hex-row > .hex:nth-child(9n-3){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 755px;
    }
    .hex-row > .hex:nth-child(11n-4){
        margin-left:67px;
    }
}

/* 7 and then 6 */
@media  only screen and (min-width: 1080px){
    .hex-row > .hex:nth-child(11n-4){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 880px;
    }
    .hex-row > .hex:nth-child(13n-5){
        margin-left:67px;
    }
}
/* 8 and then 7 */
@media only screen and (min-width: 1240px){
    .hex-row > .hex:nth-child(13n-5){
        margin-left:5px; /*cancel last break point*/
    }

    .hex-row{
        width: 1005px;
    }
    .hex-row > .hex:nth-child(15n-6){
        margin-left:67px;
    }
}

@media only print {
    .hex-fill{
         background-color: var(--primary),black !important;
         background-color: var(--accent,var(--primary),black) !important;
         -webkit-print-color-adjust: exact;
         color-adjust:exact !important;
    }
    .hex-mask > .hex-mask >.hex-mask{
        background-color: #aaa !important;
        -webkit-print-color-adjust: exact;
        color-adjust:exact !important;
    }
    .hex-icon-frame .hex-content{
        -webkit-print-color-adjust: exact;
        color-adjust:exact !important;
        background-color: var(--primary),black;
        background-color: var(--accent,var(--primary),black);
    }
    /*alternative sizeing for hex. May switch back depending on context"

    .hex-mask{
        width:150px;
        height: 180px;
    }
    .hex-pie{
        width:150px;
        height:180px;
    }
    .hex-row > .hex-pie{
        margin-top: -45px
    }
    .hex-name-plate{
        top:50px;
        left:35px;
    }
    */
    .hex-row{
        max-width:915px;
        margin:auto;
    }

    .hex-row > .hex:nth-child(15n-7){
        margin-left:67px;
    }
}

li.experience-item{
    list-style: none;
    padding-bottom: 5px;
}
ul.experience-items{
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    -webkit-padding-start:3em;
            padding-inline-start:3em;
}
li.experience-item::before{
    content: "\2b21";
    color: var(--secondary);
    display: inline-block;
    width: 1.5em;
    margin-left: -1.5em;
}
.experience-period.row{
    padding: 20px 10px;
    border-top: 1px solid var(--light);
    display:grid;
    grid-template-columns: 155px auto;
    grid-template-rows:auto;
    grid-gap: 10px;
    grid-auto-flow: row;
}
.experience-placement.row:last-child{
    border-bottom:none;
}
.experience-period .location{
    grid-column:1 / span 2;
    grid-row-end: span 1;
    font-size:20px;
    -webkit-margin-before: 0em;
            margin-block-start: 0em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
    color:var(--primary);
}
.experience-location{
    grid-column:1 / span 1;
}
.experience-title{
    color:var(--secondary);
    grid-column: 2 / span 1;
}
.experience-date{
    grid-column:1;
    grid-row-end:span 1;
    justify-self:center;
}
.experience-items{
    grid-column: 1/span 2;
}

.navbar{
    background: var(--background);
    box-shadow: 0px 3px 10px black;
    box-shadow: 0px 3px 10px var(--shadow, black);
    display:flex;
    width:100%;
    z-index:9999;
}
.navbar.top{
    position:absolute;
    top:0px;
}
.navbar.sticky{
    position:-webkit-sticky;
    position:sticky;
    top:0px;
}
.navbar.fixed{
    position:fixed;
    z-index:9999!important;
    top:0px;
}
.nav-brand{
    max-height:50px;
    margin-left: 15px;
    align-self:center;
}


.navbar.top .nav-button, .navbar.sticky .nav-button{
    padding:8px 12px;
    background:var(--background);
    color: var(--secondary),black;
    color: var(--accent,var(--secondary),black);
    border: 1px solid var(--secondary),black;
    border: 1px solid var(--accent,var(--secondary),black);
    border-left:0px;
    border-right:0px;
    display:inline-block;
    font-size: 14px;
    margin:3px 5px;

}
.navbar.top .nav-button.selected, .navbar.sticky .nav-button.selected{
    border-color: black;
    border-color: var(--primary,black);
    color:var(--primary);
}
.side-nav .nav-button {
    background:transparent;
    color: var(--background);
    width:100%;
    height:100%;
    border-bottom:1px solid var(--background);
}

li.nav-button{
    list-style: none;
}
.nav-button a{
    width:100%;
    height:100%;
}

ul.nav-list{
    list-style-type: none;
    -webkit-margin-before: 0px;
            margin-block-start: 0px;
    -webkit-margin-after: 0px;
            margin-block-end: 0px;
    -webkit-margin-start: 0px;
            margin-inline-start: 0px;
    -webkit-margin-end: 0px;
            margin-inline-end: 0px;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;

}

.navbar.top .nav-list, .navbar.sticky .nav-list{
    display:flex;
    -webkit-margin-before:none;
            margin-block-start:none;
    padding: 10px 10px;
}

.nav-link{
    text-decoration: none;
    color: inherit;
    font-size:14px;
    font-family: var(--body-font);
}

.navbar.side-nav{
    position:fixed;
    right:0px;
    top:0px;
    bottom:0px;
    width:100px;
    z-index:9999;
}
.navbar.side-nav ul{
    grid-template-columns:100px;
    display:grid;
    width:100%;
    position:relative;
}
.navbar.side-nav .nav-brand-button{
    align-items: center;
}
.navbar.side-nav .nav-brand-button img{
    max-width: 80%;
    max-height:70%;
}
.navbar.side-nav .nav-button{
    display:flex;
    width:100%;
    height:100%;
    justify-content:center;
}
.navbar.side-nav .nav-button:hover{
    background:var(--light-screen);
}
.navbar.side-nav .nav-button a{
    display:flex;
    padding: 10px 2px;
    min-height:10px;
    /* align-items:center; */
    justify-content: center;
    text-align: center;
}
.side-nav .nav-button:last-child {
    border:none;
}
.nav-shuttle{
    width:100%;
    background:var(--shadow);
    position:absolute;
}
.nav-grid .nav-brand-button{
    grid-row-start:1;
}
.nav-grid .nav-button{
    grid-row-end: span 1;
    position:relative;
    z-index:1;
}


@media only print{
    .navbar{
        display:none;
    }
}

