:root{
    --primary:#4eb310;
    --secondary:#083805;
    --accent:#ff4a4a;
    --dark:#0d170e;
    --shadow:#0d1c27;
    --light:#aab8c0;
    --background:#ffffff;
    --dark-screen:#000000e6;
    --light-screen:#fffffa36;
    --title-img:url('./TileableWatermelon2.png');
    --body-font:'Montserrat', 'Helvetica', sans-serif;
}

@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&family=Fredericka+the+Great&family=Lovers+Quarrel&family=Montserrat&family=Princess+Sofia&family=Red+Hat+Text&family=Ruthie&display=swap');
