body{
    color:var(--dark);
    background:var(--background);
}
#title{
    width:100%;
}

.App .content{
    max-width:1024px;
    margin: 0px 20px 0px 20px;
    padding: 20px 5px;
}
.title-image{
    background:var(--title-img);
    background-position:center;
    background-size:auto 200px;
    height:200px;
    width:100%;
    display:block;
}
.page-title{
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;


}
.page-title  > * {
    color:var(--dark);
    text-align: center;
    font-size:100px;
    font-family: 'Red Hat Text', sans-serif;
    font-weight: 400;
    text-shadow: 2px 3px 4px var(--shadow)
}
.page-title .first-name{
    font-size: 62px;
    display: block;
    margin-bottom: -22px;
    line-height: 47px;
    position: relative;
    z-index: 1;
}
.page-title .preferred-name{
    font-family: 'Princess Sofia', cursive;
    font-size: 112px;
    margin-right: -15px;
    line-height: 100px;
    margin-top: -15px;
    text-shadow:none;
}
.section-title{
    color:var(--pop);
    display: block;
    font-size: 2em;
    margin-block-start: 0px;
    margin-block-end: 0px;
    padding-bottom: 10px;
    font-weight: bold;
}
.content-section{
    padding:10px 10px;
    border-bottom: 1px solid var(--accent,var(--primary));
}

@media only print{
    .App .content{
        padding: 0px;
    }
    .page-title{
        background-position:center middle;
    }
}
